.root {
    color: white;
    text-transform: uppercase;
    background: #eb5e5b;
    border-radius: 4px;
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 2.4px;
    padding: 4% 5.2%;
    display: inline-block;
    white-space: nowrap;

    &_grey {
        padding: 4px 8px;

        font-size: 14px;
        line-height: 20px;
        font-weight: 600;

        letter-spacing: 0.12em;
        text-transform: uppercase;
        background: rgba(225, 232, 234, 0.52);
        border-radius: 4px;
    }
}

@media (min-width: 768px) {
    .root {
        &_grey {
            font-size: 18px;
            line-height: 24px;
        }
    }
}
