.root {
    background: #c8e6ef;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    color: #eb5e5b;
    font-weight: 600;
    font-size: 20px;
}

@media (min-width: 1440px) {
    .root {
        line-height: 64px;
        width: 64px;
        height: 64px;
    }
}
