.row {
    min-height: 700px;
    display: flex;
    justify-content: space-between;
}

.column {
    &_background {
        position: relative;
        width: 56%;
    }

    &_content {
        width: 44%;
        padding-right: 40px;
    }
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    transition: opacity 0.8s ease-in-out;

    &_show {
        opacity: 1;
    }

    &_hide {
        opacity: 0;
    }
}

.title {
    margin-bottom: 18px;
}

.description {
    margin-bottom: 24px;

    font-size: 24px;
    line-height: 32px;
    color: #697580;
}

.list {
    position: relative;
    margin-bottom: -8px;
}

.item {
    position: relative;
    padding: 16px 24px 16px 72px;
    border-radius: 32px;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;
    color: #424e58;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
        background: rgba(238, 243, 244, 0.4);
    }

    &_active {
        color: #eb5e5b;
        transition: color 0.8s ease-in-out;
        cursor: auto;

        &:hover {
            background: transparent;
        }
    }

    &_wrapp {
        padding-bottom: 8px;
    }

    &:last-child {
        &_wrapp {
            padding-bottom: 0;
        }
    }

    &-background {
        width: 100%;
        height: 104px;
        position: absolute;
        background: #eef3f4;
        backdrop-filter: blur(40px);
        border-radius: 32px;
        overflow: hidden;
        transition: top 0.8s ease-in-out, height 0.8s ease-in-out;
    }

    &_title {
        margin-bottom: 0;
        font-size: 28px;
        line-height: 36px;
        font-weight: 600;
    }
}

.point {
    position: absolute;
    top: 24px;
    left: 25px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: #eef3f4;
    transition: all 0.8s ease-in-out;

    &_active {
        background: #eb5e5b;
    }
}
