.root {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.reverse {
    flex-direction: column-reverse;

    .patient-image {
        margin-bottom: 0;
    }

    .patient-feedback-text {
        margin-bottom: 40px;
    }
}

.patient-feedback-text {
    flex: 1;
    flex-grow: 0;
}

.patient-image {
    height: 100%;
    margin-bottom: 40px;

    picture {
        height: 100%;
    }

    &__image {
        object-fit: cover;
        width: 100%;
        display: block;
        height: 100%;
    }
}
