@import "style/mixins.module.scss";

.root {
    padding: 24px;
    background: #eef3f4;
}

.feedback-head {
    margin-bottom: 16px;
}

.feedback-text {
    font-size: 17px;
    line-height: 24px;
    margin-bottom: 0;
    padding-right: 32px;
    padding-left: 8px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
        padding-right: 0;
        padding-left: 0;
    }
}
