// TODO https://github.com/smile2impress/frontend/issues/176
$borderRadius: 8px;

.root {
    border-radius: $borderRadius;
    min-height: 328px;
    background-position: center right;
    background-size: cover;
}

.gradient {
    position: relative;
    border-radius: $borderRadius;
    overflow: hidden;
    padding: 20px 19px;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: radial-gradient(
            87.4% 107.46% at 0% 0%,
            rgba(84, 72, 53, 0.81) 15.64%,
            rgba(0, 0, 0, 0.33) 62.4%,
            rgba(230, 232, 226, 0) 100%
        );
        mix-blend-mode: multiply;
    }
}

.number {
    position: relative;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
    margin: 0;

    font-size: 20px;
    color: #fff;
    font-weight: 600;
    z-index: 1;
}

.title {
    position: relative;
    font-weight: 600;
    color: #fff;
    font-size: 20px;
    margin-top: 16px;
    margin-left: 4px;
    line-height: 28px;
    z-index: 1;
}

.text {
    position: relative;
    margin: 0;
    font-size: 20px;
    line-height: 28px;
    color: #fff;
    margin-top: -5px;
    margin-left: 4px;
    z-index: 1;
}

.badge {
    padding: 1.3% 2.5%;
    margin-top: 11px;
    font-size: 14px;
    letter-spacing: 1.7px;
    margin-left: 4px;
}

@media (min-width: 375px) {
    .text,
    .title {
        max-width: 65%;
    }
}
