.root {
    // TODO https://github.com/smile2impress/frontend/issues/176
    :global .react-multi-carousel-list {
        padding-bottom: 44px;
    }

    :global .react-multi-carousel-dot {
        outline: none;
        color: #eb5e5b;
    }

    :global .react-multi-carousel-dot button {
        width: 12px;
        height: 12px;

        border: none;
        background: #fdefef;
        margin-right: 8px;
        outline: none;
    }

    :global .react-multi-carousel-dot--active button {
        background: #eb5e5b;
    }

    &_with-margin {
        margin-right: 24px;
        margin-left: -24px;
    }
}

.image {
    width: 100%;
    height: 316px;
    object-fit: cover;

    &_with-margin {
        margin-bottom: 24px;
    }
}

.patient-feedback-text {
    background: none;
    padding: 0;
}
