@import "style/mixins.module.scss";

.about,
.steps,
.prices,
.advantages,
.appointment,
.patient-feedback {
    margin-bottom: 80px;
}

.offer {
    margin-bottom: 40px;
}

.question {
    padding-bottom: 140px;
}

.appointment-title {
    @include formTitle();
}

@media (min-width: 768px) {
    .about,
    .steps,
    .prices,
    .advantages,
    .appointment,
    .patient-feedback {
        margin-bottom: 160px;
    }

    .feature-check-list {
        border-bottom: 1px solid #c8e6ef;
    }

    .question {
        padding-bottom: 240px;
    }
}

.head {
    min-height: 596px;
    margin-bottom: 64px;

    @include respond-to("laptop") {
        margin-bottom: 160px;
    }
}
