.root {
    position: relative;
    padding: 40px 24px 45px;
    border-radius: 8px;
    overflow: hidden;
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    transition: opacity 1s ease-in-out;

    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 85%;
        height: 100%;
        transition: opacity 0.5s ease-in-out;
    }

    &_1 {
        &:after {
            background: radial-gradient(
                102.14% 161.11% at 0% 30.91%,
                #2b1f10 0%,
                rgba(55, 53, 49, 0.4) 66.04%,
                rgba(226, 240, 249, 0) 99.7%
            );
            mix-blend-mode: multiply;
        }
    }

    &_2 {
        &:after {
            background: radial-gradient(
                102.14% 161.11% at 0% 30.91%,
                #362510 0%,
                rgba(45, 46, 40, 0.2) 66.04%,
                rgba(226, 240, 249, 0) 99.7%
            );
            mix-blend-mode: multiply;
        }
    }

    &_3 {
        &:after {
            background: radial-gradient(
                102.14% 161.11% at 0% 30.91%,
                #120604 0%,
                rgba(97, 30, 9, 0.4) 66.04%,
                rgba(226, 240, 249, 0) 99.7%
            );
            mix-blend-mode: multiply;
        }
    }

    &_show {
        opacity: 1;

        &:after {
            opacity: 1;
        }
    }

    &_hide {
        opacity: 0;

        &:after {
            opacity: 0;
        }
    }
}

.content {
    width: 584px;
    color: #fff;
}

.item,
.title,
.button {
    position: relative;
    z-index: 10;
}

.button {
    display: none;
    margin-left: 84px;
}

.title {
    margin-bottom: 26px;
    padding-left: 66px;
}

.list {
    position: relative;
}

.item {
    padding: 9px 24px 15px 66px;
    border-radius: 32px;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
        background: rgba(255, 255, 255, 0.1);
    }

    &_wrapp {
        padding-bottom: 15px;
    }

    &_active {
        cursor: auto;

        &:hover {
            background: transparent;
        }
    }

    &-background {
        width: 100%;
        height: 162px;
        z-index: 1;
        position: absolute;
        background: rgba(255, 255, 255, 0.32);
        backdrop-filter: blur(40px);
        border-radius: 32px;
        overflow: hidden;
        transition: top 1s ease-in-out, height 1s ease-in-out;
    }

    &_number {
        position: absolute;
        top: 8px;
        left: 8px;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.2);
        font-size: 20px;
        font-weight: 600;
        line-height: 1;
        margin: 0;
    }

    &_title {
        margin-bottom: 8px;
        font-size: 20px;
        line-height: 28px;
        font-weight: 600;
    }

    &_text {
        margin-bottom: 0;
        font-size: 24px;
        line-height: 32px;
    }

    &_label {
        display: none;
    }
}

@media (min-width: 1280px) {
    .root {
        padding: 40px 24px 76px;
    }

    .content {
        width: 628px;
    }

    .item {
        padding: 14px 40px 20px 84px;

        &_title {
            font-size: 28px;
            line-height: 36px;
        }

        &_label {
            display: inline-block;
            margin-top: 8px;
            margin-bottom: 0;
            padding: 4px 8px;
            font-size: 18px;
            line-height: 24px;
            font-weight: 700;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: #fff;
            background: #eb5e5b;
            border-radius: 4px;
        }

        &_number {
            width: 48px;
            height: 48px;
            font-size: 28px;
            font-weight: 600;
        }
    }

    .title {
        padding-left: 84px;
    }

    .button {
        display: block;
    }
}
