@import "style/mixins.module.scss";

.root {
    position: relative;
    background: url("/special/images/new/universal-spain/bg-head-mobile.webp")
        no-repeat center top;
    background-size: cover;

    color: #fff;

    &.no-webp {
        background-image: url("/special/images/new/universal-spain/bg-head-mobile.jpg");
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        mix-blend-mode: multiply;
    }
}

.column {
    position: relative;
    padding-top: 380px;
    max-width: 386px;
    z-index: 1;

    @include respond-to("laptop") {
        padding-top: 288px;
    }
}

.content {
    margin-bottom: 16px;
    padding-bottom: 40px;

    @include respond-to("laptop") {
        margin-bottom: 0;
    }
}

.title {
    &-block {
        margin-bottom: 10px;
    }

    &_line {
        display: inline-block;

        &:after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 38%;
            width: 100%;
            height: 3px;
            transform: rotate(-1.5deg);
            background-color: #eb5e5b;
        }
    }
}

.subtitle {
    margin-bottom: 0;
    font-size: 22px;
    line-height: 32px;
    font-weight: 400;
}

.button {
    display: none;
}

.description {
    margin-bottom: 0;
    padding-bottom: 16px;
    font-size: 14px;
    line-height: 20px;
}

.container {
    margin-bottom: 80px;
}

@media (min-width: 520px) {
    .root {
        background-image: url("/special/images/new/universal-spain/bg-head-tablet.webp");

        &.no-webp {
            background-image: url("/special/images/new/universal-spain/bg-head-tablet.jpg");
        }
    }
}

@media (min-width: 768px) {
    .root {
        &:before {
            background: radial-gradient(
                33.99% 71.68% at 78.47% 44.77%,
                rgba(239, 228, 225, 0) 0%,
                rgba(66, 78, 88, 0.616) 100%
            );
        }

        &_margin {
            margin-bottom: 120px;
        }
    }

    .column {
        min-height: 536px;

        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    .content {
        margin-bottom: 32px;

        padding-bottom: 0px;
    }

    .button {
        display: block;
        margin-bottom: 72px;
    }

    .title {
        &-block {
            margin-bottom: 16px;
        }

        &_line {
            &:after {
                height: 5px;
            }
        }
    }

    .subtitle {
        max-width: 100%;
    }

    .container {
        margin-bottom: 0;
    }
}

@media (min-width: 1024px) {
    .root {
        background-image: url("/special/images/new/universal-spain/bg-head-desktop.webp");

        &.no-webp {
            background-image: url("/special/images/new/universal-spain/bg-head-desktop.jpg");
        }
    }

    .column {
        max-width: 687px;
    }

    .description {
        padding-bottom: 20px;
        font-size: 20px;
        line-height: 28px;
    }
}

@media (min-width: 1366px) {
    .root {
        background-image: url("/special/images/new/universal-spain/bg-head-desktop-L.webp");

        &.no-webp {
            background-image: url("/special/images/new/universal-spain/bg-head-desktop-L.jpg");
        }
    }
}
