@import "style/mixins.module.scss";

.column {
    margin-bottom: 56px;
    text-align: center;

    &:last-child {
        margin-bottom: 0;
    }

    @include respond-to("laptop") {
        width: 33.333%;
        padding: 0 20px;
        text-align: left;
    }
}

.image {
    display: inline-block;
    width: 120px;
    height: 120px;
    margin-bottom: 16px;
    border-radius: 50%;

    @include respond-to("laptop") {
        width: 160px;
        height: 160px;
    }
}

.title {
    @include respond-to("laptop") {
        margin-bottom: 16px;
    }
}

.content {
    display: none;

    @include respond-to("laptop") {
        display: block;
        font-size: 22px;
        line-height: 32px;
        margin-bottom: 0;
    }
}

.row {
    @include respond-to("laptop") {
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin: 0 -20px;
    }
}
