@import "style/mixins.module.scss";

.root {
    width: calc(100% + 24px);

    @include respond-to("laptop") {
        width: 100%;
    }
}

.title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 64px;
    }
}

.patient-feedback-list {
    display: none;
}

// TODO https://github.com/smile2impress/frontend/issues/176
@media (min-width: 880px) {
    .patient-feedback-slider {
        display: none;
    }

    .patient-feedback-list {
        display: flex;
    }
}
