.column {
    &_list {
        position: relative;
        min-height: 328px;
        padding: 29px 160px 0 32px;
        border-radius: 8px;
        overflow: hidden;
        background: url("/special/images/new/christmas/mob-about-bg.jpg")
            no-repeat center center;
        background-size: cover;
        color: #fff;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: linear-gradient(
                270deg,
                rgba(66, 78, 88, 0) 49.55%,
                rgba(66, 78, 88, 0.48) 88.01%
            );
            mix-blend-mode: multiply;
        }
    }

    &_content {
        text-align: center;
        color: #697580;
    }
}

.title {
    margin-bottom: 16px;
}

.description {
    margin-bottom: 32px;

    font-size: 20px;
    line-height: 28px;
}

.item_title {
    position: relative;
    width: 148px;
    margin-bottom: 24px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2.2px;
    text-transform: uppercase;
    z-index: 1;
}

@media (min-width: 768px) {
    .row {
        display: flex;
        justify-content: space-between;
    }

    .column {
        width: 50%;

        &_content {
            padding-right: 72px;
            text-align: left;
        }
    }
}
