@import "style/mixins.module.scss";

.root {
    display: flex;
}

.text-section {
    margin-left: 16px;
}

.name {
    margin-bottom: 0;
}

.description {
    color: #abb4bb;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 0;
    line-height: 1;

    @include respond-to("laptop") {
        font-size: 20px;
        line-height: 28px;
    }
}
